<script>
  import {
    Button,
    Form,
    Input,
    ButtonDropdown,
    DropdownMenu,
    DropdownToggle,
    FormGroup,
  } from "sveltestrap";

  import { _ } from "svelte-i18n";

  import { Link } from "svelte-routing";

  import LanguageDropdown from "../../common/LanguageDropdown.svelte";
  import NotificationDropdown from "../../common/NotificationDropdown.svelte";
  import ProfileMenu from "../../common/ProfileMenu.svelte";
  import RightsidebarButton from "../../Components/RightsidebarButton.svelte";

  function toggleFullscreen() {
    if (
      !document.fullscreenElement &&
      /* alternative standard method */ !document.mozFullScreenElement &&
      !document.webkitFullscreenElement
    ) {
      // current working methods
      if (document.documentElement.requestFullscreen) {
        document.documentElement.requestFullscreen();
      } else if (document.documentElement.mozRequestFullScreen) {
        document.documentElement.mozRequestFullScreen();
      } else if (document.documentElement.webkitRequestFullscreen) {
        document.documentElement.webkitRequestFullscreen(
          Element.ALLOW_KEYBOARD_INPUT
        );
      }
    } else {
      if (document.cancelFullScreen) {
        document.cancelFullScreen();
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen();
      } else if (document.webkitCancelFullScreen) {
        document.webkitCancelFullScreen();
      }
    }
  }

  function tToggle() {

    var body = document.body;

    body.classList.toggle("sidebar-enable");

	if(window.innerWidth >= 992 ){
		if(body.getAttribute("data-sidebar-size") == 'sm'){
			body.setAttribute("data-sidebar-size", "lg");
		}else{
			body.setAttribute("data-sidebar-size", "sm");
		}

	}

  }

  export let sidebar = false;

</script>

<header id="page-topbar">
  <div class="navbar-header">
    <div class="d-flex">
      <!-- LOGO -->
      <div class="navbar-brand-box">
        <Link to={"/"} class="logo logo-dark">
          <span class="logo-sm">
            <img src="assets/images/logo-sm.png" alt="" height="22" />
          </span>
          <span class="logo-lg">
            <img src="assets/images/logo-dark.png" alt="" height="23" />
          </span>
        </Link>

        <Link to={"/"} class="logo logo-light">
          <span class="logo-sm">
            <img src="assets/images/logo-sm-light.png" alt="" height="22" />
          </span>
          <span class="logo-lg">
            <img src="assets/images/logo-light.png" alt="" height="23" />
          </span>
        </Link>
      </div>

      <button
        class="btn btn-sm px-3 font-size-16 vertinav-toggle header-item waves-effect"
        id="vertical-menu-btn"
        on:click={tToggle}
      >
        <i class="fa fa-fw fa-bars" />
      </button>

      <button
        class="btn btn-sm px-3 font-size-16 horinav-toggle header-item waves-effect waves-light"
        data-bs-toggle="collapse"
        data-bs-target="#topnav-menu-content"
      >
        <i class="fa fa-fw fa-bars" />
      </button>

      <!-- App Search-->
      <Form class="app-search d-none d-lg-block">
        <div class="position-relative">
          <Input
            type="text"
            class="form-control"
            placeholder={$_("navbar.search.text")}
          />
          <span class="mdi mdi-magnify" />
        </div>
      </Form>
    </div>

    <div class="d-flex">
      <ButtonDropdown class="d-inline-block d-lg-none ms-2">
        <DropdownToggle
          color="primary"
          class="header-item noti-icon waves-effect"
          ><i class="mdi mdi-magnify" /></DropdownToggle
        >
        <DropdownMenu class="dropdown-menu-lg dropdown-menu-end p-0">
          <Form class="p-3">
            <FormGroup class="m-0">
              <div class="input-group">
                <Input
                  type="text"
                  class="form-control"
                  placeholder={$_("navbar.search.text")}
                  aria-label="Recipient's username"
                />
                <div class="input-group-append">
                  <Button class="btn btn-primary" type="submit"
                    ><i class="mdi mdi-magnify" /></Button
                  >
                </div>
              </div>
            </FormGroup>
          </Form>
        </DropdownMenu>
      </ButtonDropdown>

      <LanguageDropdown />

      <div class="dropdown d-none d-lg-inline-block ms-1">
        <button
          type="button"
          class="btn header-item noti-icon"
          on:click={toggleFullscreen}
          data-toggle="fullscreen"
        >
          <i class="bx bx-fullscreen" />
        </button>
      </div>

      <NotificationDropdown />

      <ProfileMenu />

      <div class="dropdown d-inline-block">
        <RightsidebarButton bind:open={sidebar} />
      </div>
    </div>
  </div>
</header>
