<script>
	import { auth } from '../../helpers/firebase';
	import { navigate } from 'svelte-routing';
	import { Container, Row, Col } from 'sveltestrap';
	import { Link } from 'svelte-routing';


    const handleResetLink = ({
		target: {
			elements: { email },
		},
	}) => {
		auth.sendPasswordResetEmail(email.value)
			.then(() => {

                this.emailSending = false;

				alert('Password reset email sent successfully')

                navigate('/login');
			})
			.catch(() => {
				setError('Error resetting password');
			});
	};

</script>

<div class="account-pages my-5 pt-sm-5">
	<Container>
		<Row class="justify-content-center">
			<Col md={10}>
				<div class="text-center mb-5">
					<a href="/" class="auth-logo">
						<img src="assets/images/logo-dark.png" alt="" height="28" class="auth-logo-dark" />
						<img src="assets/images/logo-light.png" alt="" height="28" class="auth-logo-light" />
					</a>
					<p class="font-size-15 text-muted mt-3">Responsive <b>Bootstrap 5</b> Admin Dashboard</p>
				</div>
				<div class="card overflow-hidden">
					<Row class="g-0">
						<Col lg={6}>
							<div class="p-lg-5 p-4">
								<div>
									<h5>Reset Password</h5>
									<p class="text-muted">Re-Password with Samply.</p>
								</div>

								<div class="mt-4 pt-3">
									<!-- <div class="alert alert-success mb-4" role="alert">
										Enter your Email and instructions will be sent to you!
									</div> -->

									<form on:submit|preventDefault={handleResetLink}>
										<div class="mb-3">
											<label class="fw-semibold" for="useremail">Email</label>
											<input
												type="email"
												name="email"
												class="form-control"
												id="useremail"
												placeholder="Enter email"
											/>
										</div>

										<div class="mt-4 text-end">
											<button class="btn btn-primary w-md waves-effect waves-light" type="submit">Reset</button>
										</div>
									</form>
								</div>
							</div>
						</Col>
						<Col lg={6}>
							<div class="p-lg-5 p-4 bg-auth h-100 d-none d-lg-block">
								<div class="bg-overlay" />
							</div>
						</Col>
					</Row>
				</div>
			
				<div class="mt-5 text-center">
					<p>
						Go back to{' '}
						<Link to="auth-login" class="font-weight-medium text-primary">Login</Link>{' '}
					</p>
					<p>
						© {new Date().getFullYear()} Skote. Crafted with{' '}
						<i class="mdi mdi-heart text-danger" /> by Themesbrand
					</p>
				</div>
			</Col>
			
		</Row>
		
	</Container>
	
</div>
