<script>
	import { auth } from '../../helpers/firebase';
	import { navigate } from 'svelte-routing';
	import { user } from '../../helpers/store.js';
	import { Container } from 'sveltestrap';
	import { Link } from 'svelte-routing';

	let email = 'admin@themesbrand.com';
	let password = '123456';

	// Destructuring to obtain email and password from form via Event
	const handleLoginForm = () => {
		auth.signInWithEmailAndPassword(email, password)
			.then(function (result) {
				let firebaseUser = auth.currentUser;

				if (firebaseUser) {
					let { email } = firebaseUser;

					user.set({ ...$user, loggedIn: true, email });

					navigate('/');
				}
			})
			.catch((error) => alert(error.message));
	};

</script>

<div class="account-pages my-5 pt-sm-5">
	<Container>
		<div class="row justify-content-center">
			<div class="col-md-10">
				<div class="text-center mb-5">
					<a href={'/'} class="auth-logo">
						<img src="assets/images/logo-dark.png" alt="" height="28" class="auth-logo-dark" />
						<img src="assets/images/logo-light.png" alt="" height="28" class="auth-logo-light" />
					</a>
					<p class="font-size-15 text-muted mt-3">Responsive <b>Bootstrap 5</b> Admin Dashboard</p>
				</div>
				<div class="card overflow-hidden">
					<div class="row g-0">
						<div class="col-lg-6">
							<div class="p-lg-5 p-4">
								<div>
									<h5>Welcome Back !</h5>
									<p class="text-muted">Sign in to continue to Samply.</p>
								</div>

								<div class="mt-4 pt-3">
									<div class="mb-3">
										<label for="email" class="fw-semibold">Email</label>
										<input
											type="email"
											class="form-control"
											id="email"
											placeholder="Enter email"
											bind:value={email}
										/>
									</div>

									<div class="mb-3 mb-4">
										<label for="password" class="fw-semibold">Password</label>
										<input
											type="password"
											class="form-control"
											id="password"
											placeholder="Enter password"
											bind:value={password}
										/>
									</div>

									<div class="row align-items-center">
										<div class="col-6">
											<div class="form-check">
												<input
													type="checkbox"
													class="form-check-input font-size-16"
													id="remember-check"
												/>
												<label class="form-check-label" for="remember-check">Remember me</label>
											</div>
										</div>
										<div class="col-6">
											<div class="text-end">
												<button
													class="btn btn-primary w-md waves-effect waves-light"
													on:click={handleLoginForm}>Login</button
												>
											</div>
										</div>
									</div>

									<div class="mt-4">
										<a href="auth-recoverpw" class="text-muted"
											><i class="mdi mdi-lock me-1" /> Forgot your password?</a
										>
									</div>
								</div>
							</div>
						</div>

						<div class="col-lg-6">
							<div class="p-lg-5 p-4 bg-auth h-100 d-none d-lg-block">
								<div class="bg-overlay" />
							</div>
						</div>
					</div>
				</div>
				<!-- end card -->
				<div class="mt-5 text-center">
					<p>
						Don&#39;t have an account ?{' '}
						<Link to="auth-register" class="fw-medium text-primary">
							{' '}
							Signup now{' '}
						</Link>{' '}
					</p>
					<p>
						© {new Date().getFullYear()} Pichforest. Crafted with{' '}
						<i class="mdi mdi-heart text-danger" /> by Themesbrand
					</p>
				</div>
			</div>
		</div>
	</Container>
</div>
