<script>
	import { auth } from '../../helpers/firebase';
	import { navigate } from 'svelte-routing';
	import { user } from '../../helpers/store.js';
    import { Container, Row, Col, Card} from 'sveltestrap';
    import { Link } from 'svelte-routing';

	// Destructuring to obtain email and password from form via Event
	const handleRegisterForm = ({
		target: {
			elements: { email, password },
		},
	}) => {
		auth.createUserWithEmailAndPassword(email.value, password.value).catch((error) => alert(error.message));
		let firebaseUser = auth.currentUser;

		if (firebaseUser) {
			let { email } = firebaseUser;
			
			user.set({ ...$user, loggedIn: true, email });
			
			navigate('/');
		}
	};

</script>

<div class="account-pages my-5 pt-sm-5">
	<Container>
		<Row class="justify-content-center">
			<Col md={10}>
				<div class="text-center mb-5">
					<a href={"/"} class="auth-logo">
						<img src="assets/images/logo-dark.png" alt="" height="28" class="auth-logo-dark" />
						<img src="assets/images/logo-light.png" alt="" height="28" class="auth-logo-light" />
					</a>
					<p class="font-size-15 text-muted mt-3">Responsive <b>Bootstrap 5</b> Admin Dashboard</p>
				</div>
				<Card class="overflow-hidden">
					<Row class="g-0">
						<Col lg={6}>
							<div class="p-lg-5 p-4">
								<div>
									<h5>Register account</h5>
									<p class="text-muted">Get your free Samply account now.</p>
								</div>

								<div class="mt-4 pt-3">
									<form on:submit|preventDefault={handleRegisterForm}>
										<div class="mb-3">
											<label class="fw-semibold" for="email">Email</label>
											<input
												type="email"
                                                name="email"
												class="form-control"
												id="email"
												placeholder="Enter email"
											/>
										</div>

										<div class="mb-3">
											<label class="fw-semibold" for="username">Username</label>
											<input
												type="text"
                                                name="Username"
												class="form-control"
												id="username"
												placeholder="Enter username"
											/>
										</div>

										<div class="mb-3">
											<label class="fw-semibold" for="password">Password</label>
											<input
												type="password"
                                                name="password"
												class="form-control"
												id="password"
												placeholder="Enter password"
											/>
										</div>

										<div class="mt-4 text-end">
											<button class="btn btn-primary w-md waves-effect waves-light" type="submit"
												>Register</button>
										</div>

										<div class="mt-4 text-center">
											<p class="mb-0 text-muted">
												By registering you agree to the Samply <Link
													to={'#'}
													class="text-primary fw-semibold text-decoration-underline"
													>Terms of Use</Link>
											</p>
										</div>
									</form>
								</div>
							</div>
						</Col>
						<div class="col-lg-6">
							<div class="p-lg-5 p-4 bg-auth h-100 d-none d-lg-block">
								<div class="bg-overlay" />
							</div>
						</div>
					</Row>
				</Card>
				
				<div class="mt-5 text-center">
					<p>
						Already have an account ? 
                        <Link to="auth-login"
							class="fw-semibold text-decoration-underline"
						>
							Login
						</Link>
					</p>

					<p>
						© {new Date().getFullYear()} Samply. Crafted with <i class="mdi mdi-heart text-danger" /> by Pichforest
					</p>

				</div>
			</Col>
			<!-- end col -->
		</Row>
		<!-- end row -->
	</Container>
	<!-- end container -->
</div>