<script>
	import { _ } from 'svelte-i18n';
	import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'sveltestrap';
	import { user } from '../helpers/store';
	import { auth } from '../helpers/firebase';
	import { navigate } from 'svelte-routing';

	const handleLogOut = () => {
		auth.signOut().then(
			function () {
				// Sign-out successful.
				user.set({ ...$user, loggedIn: false });
				
				navigate('/auth-login');
			},
			function (error) {
				// An error happened.
				console.warn('error on logout', error.message);
			}
		);
	};

	

</script>

<Dropdown class="dropdown d-inline-block">
	<DropdownToggle class="btn header-item " id="page-header-user-dropdown" tag="button" color="">
		<img class="rounded-circle header-profile-user" src="assets/images/users/avatar-1.jpg" alt="Header Avatar" />
		<span class="d-none d-xl-inline-block ms-1"> {$_('navbar.dropdown.profile.text')} </span>
		<i class="mdi mdi-chevron-down d-none d-xl-inline-block" />
	</DropdownToggle>

	<DropdownMenu class="dropdown-menu-end">
		<h6 class="dropdown-header">{$_('navbar.dropdown.profile.list.welcome')}</h6>
		<DropdownItem tag="a" href={'#'}
			><i class="mdi mdi-account-circle text-muted font-size-16 align-middle me-1" />
			<span class="align-middle" key="t-profile"> admin </span>
		</DropdownItem>
		<DropdownItem tag="a" href={'#'}
			><i class="mdi mdi-message-text-outline text-muted font-size-16 align-middle me-1" />
			<span class="align-middle" key="t-messages">{$_('navbar.dropdown.profile.list.messages')}</span>
		</DropdownItem>
		<DropdownItem tag="a" href={'#'}
			><i class="mdi mdi-calendar-check-outline text-muted font-size-16 align-middle me-1" />
			<span class="align-middle" key="t-taskboard">{$_('navbar.dropdown.profile.list.taskboard')}</span>
		</DropdownItem>
		<DropdownItem tag="a" href={'#'}
			><i class="mdi mdi-lifebuoy text-muted font-size-16 align-middle me-1" />
			<span class="align-middle" key="t-help">{$_('navbar.dropdown.profile.list.help')}</span>
		</DropdownItem>
		<div class="dropdown-divider" />
		<DropdownItem tag="a" href={'#'}
			><i class="mdi mdi-wallet text-muted font-size-16 align-middle me-1" />
			<span class="align-middle" key="t-balance"
				>{$_('navbar.dropdown.profile.list.balance.text')} :
				<b>{$_('navbar.dropdown.profile.list.balance.amount')}</b></span
			>
		</DropdownItem>
		<DropdownItem tag="a" href={'#'}
			><span class="badge bg-success bg-soft text-success mt-1 float-end"
				>{$_('navbar.dropdown.profile.list.settings.badge')}</span
			><i class="mdi mdi-cog-outline text-muted font-size-16 align-middle me-1" />
			<span class="align-middle" key="t-settings">{$_('navbar.dropdown.profile.list.settings.text')}</span>
		</DropdownItem>
		<DropdownItem tag="a" href={'/auth-lock-screen'}
			><i class="mdi mdi-lock text-muted font-size-16 align-middle me-1" />
			<span class="align-middle" key="t-lock-screen">{$_('navbar.dropdown.profile.list.lockscreen')}</span>
		</DropdownItem>
		<DropdownItem tag="a" on:click={handleLogOut}
			><i class="mdi mdi-logout text-muted font-size-16 align-middle me-1" />
			<span class="align-middle" key="t-logout">{$_('navbar.dropdown.profile.list.logout')}</span>
		</DropdownItem>
	</DropdownMenu>
</Dropdown>